export default {
	data() {
		return {
			shopid: 0,
			name: '',
			people:''
		}
	},
	onLoad:function(option){
		this.shopid = option.shopid
	},
	methods: {
		addtable:function(){
			var that = this
			uni.request({
				url: this.$bashUrl+ '/index.php/home/Api/new_table/',
				method: 'POST',
				data:{
					id: this.shopid,
					name: this.name,
					num: this.people
				},
				success:(res=>{
					console.log(res)
					uni.showToast({
						title: '桌子添加成功',
						icon:'none'
					});
					setTimeout(function () {
					    uni.navigateBack({
					    	delta:1
					    })
					}, 1000)
				}),fail:(err=>{
					uni.showToast({
						title: '未添加成功',
						icon:'none'
					});
				})
			})
		}
	}
}
